___CSS_LOADER_EXPORT___.push([module.id, `ul,
li {
    list-style: none;
}

.form-wrapper {
    display: flex;
    justify-content: center;
}

.contact-form {
    border: 2px solid var(--clr-2);
    border-radius: 10px;
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    padding: 40px;
    max-width: 800px;
}

.contact-form li * {
    display: block;
}

.contact-form li {
    flex: 1;
    &.first-wrapper,
    &.last-wrapper,
    &.home-wrapper,
    &.work-wrapper {
        flex-basis: 180px;
    }
    &.email-wrapper {
        flex-basis: 250px;
    }
    &.location-wrapper {
        flex-basis: 200px;
    }
}

.contact-form li.comments-wrapper {
    min-width: 100%;
    textarea {
        min-width: 100%;
        min-height: 10em;
        line-height: 1.6em;
        resize: none;
    }
}

.contact-form input,
.contact-form textarea,
.contact-form select {
    background-color: var(--text-clr);
    width: 100%;
    height: 2em;
    padding: 0 4px;
    color: var(--bg-clr);
    position: relative;
}

label:has(+ input:required)::after,
label:has(+ textarea:required)::after {
    content: "*";
    color: rgba(255, 0, 0, 0.7);
}

:required:after {
    content: "*";
    top: 0;
    right: calc(100% + 1px);
    width: 100%;
    height: 100%;
    background-color: #33fbb8;
    z-index: 99;
    display: block;
}

.contact-form button {
    font-size: 1.2em;
    margin-top: 10px;
    padding: 8px 16px;
    border: none;
    border-radius: 20px;
    background-color: var(--clr-1);
}
