___CSS_LOADER_EXPORT___.push([module.id, `.menu-section-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    &:not(:first-child) {
        padding-top: 80px;
    }
}

.menu-section-header {
    font-size: 3em;
}

.menu-item-box {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 40px;
}

.menu-item-card {
    border: 2px solid var(--clr-2);
    border-radius: 10px;
    padding: 30px;
    min-width: 300px;
    max-width: 500px;
    min-height: 200px;
    font-style: italic;
    display: grid;
    place-items: center;
}

.menu-item-name {
    font-size: 24px;
    margin-bottom: 8px;
    font-weight: 700;
}

.menu-item-price {
    color: var(--clr-1);
}
