___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --bg-clr: #28282b; /* Matte Black */
    --footer-clr: hsl(240, 4%, 10%); /* Matte Black */
    --text-clr: #fffff0; /* Ivory */
    --clr-1: #4997d0; /* Celestial Blue */
    --clr-2: #e1bc28; /* Saffron */
    font-family: "Montserrat", serif;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    color: var(--text-clr, black);
    background-color: var(--bg-clr, white);
}

body > * {
    margin-left: 12%;
    margin-right: 12%;
}

#content > * {
    margin-bottom: 80px;
    padding-top: 80px;
    &:not(:first-child) {
        border-top: 1px solid var(--text-clr);
    }
}

button {
    cursor: pointer;
    &:hover {
        scale: 1.05;
        color: var(--clr-2);
    }
}

/* Header */

header {
    margin: 0;
    margin-bottom: 10px;
    border-bottom: 1px solid var(--text-clr);
}

header nav {
    display: flex;
    justify-content: center;
    gap: 10vw;
    align-items: center;
    height: 100px;
    padding-bottom: 10px;
}

header nav button {
    border: none;
    font-size: 1.5em;
    padding: 20px;
}

/* Footer */

footer {
    display: flex;
    justify-content: space-around;
    border-top: 1px solid var(--text-clr);
    margin: 0;
    padding: 10px 0;
    a {
        background-color: inherit;
    }
}
